<template>
    <div class="neraca">
        <vx-card class="mb-5">
            <div class="md:flex">
                <div class="lg:w-1/4 mt-4 md:mt-0 md:mr-6">
                    <datepicker :minimumView="'month'" :maximumView="'year'" :format="'MMM yyyy'" v-model="selectedMonth"></datepicker>
                </div>
                <!-- <div class="lg:w-1/4 mt-4 md:mt-0 md:mx-6">
                    <datepicker :minimumView="'month'" :maximumView="'year'" :format="'MMM yyyy'" v-model="selectedMonthEnd"></datepicker>
                </div> -->
                <div class="lg:w-auto sm:w-full mt-3 md:mr-6">
                    <vs-radio v-model="option" vs-name="Multi Entitas" vs-value="ME">Multi Entitas</vs-radio>
                </div>  
                <div class="lg:w-32 sm:w-full mt-3 md:mr-6">
                    <vs-radio v-model="option" vs-name="Multi Bulan" vs-value="MB" >Multi Bulan</vs-radio>
                </div>
                <div class="mt-4 md:mt-0">
                    <vs-button radius color="primary" icon="search" v-on:click="loadDataServer"  style="display:inline;"></vs-button>
                    <vs-button class="mx-6" :disabled="excell" @click="tableToExcel('table', 'AKUN')" type="border"><i class="feather icon-file-text"></i></vs-button>
                </div>  
            </div>
        </vx-card>

        <div class="md:flex ">
            <vx-card class="md:w-1/4 lg:w-1/4 sm:w-full mr-2 mb-4">
                <vx-input-group class="mb-base" prependClasses="border border-solid border-grey border-r-0">
                    <template slot="prepend">
                        <div class="prepend-text">
                            <vs-checkbox v-model="selectAll" ><span class="text-primary"></span></vs-checkbox>
                        </div>
                    </template>
                    <vs-input class="inputx" placeholder="Search" v-model="search" />
                </vx-input-group>
                <div style="height:1617px; overflow-y:auto">
                    <ul class="centerx">
                        <li v-for="(tr, index) in filteredItems" :key="index" class="p-2">
                        <vs-checkbox v-model="selected" :vs-value="tr.kode">{{ tr.nama }}</vs-checkbox>
                        </li>
                    </ul>
                </div>
            </vx-card>

            <vx-card class="lg:w-3/4 sm:w-full mb-4" v-show="table > []">
                <div style="width:w-4/5; overflow-x:auto">
                    <div class="zui-wrapper">
                        <div class="zui-scroller">
                            <div hidden ref="table" id="loremTable" summary="lorem ipsum sit amet" rules="groups" frame="hsides">
                                <h4>Bulan : {{selectedMonth | moment("MMMM YYYY") }}</h4>
                                <table class="zui-table">
                                    <thead class="fixedHeader">
                                        <tr  class="bg-primary text-white">
                                            <th  v-for="(data,index) in header" :key="index" class=" bg-primary text-white">{{data}}</th>
                                        </tr>
                                    </thead>
                                    <tbody class="scrollContent">
                                        <tr v-for="(value,key) in table" :key="key" >
                                            <td  v-for="(data,index) in value" :key="index" style="height:34px;"  v-html="data"></td>
                                        </tr>
                                    </tbody>
                                </table>
                            </div>

                            <table class="zui-table" >
                                <thead class="fixedHeader">
                                    <tr  class="bg-primary text-white">
                                        <th  v-for="(items,index) in header" :key="index" class=" bg-primary text-white">{{items}}</th>
                                    </tr>
                                </thead>
                                <tbody class="scrollContent">
                                    <tr v-for="(value,key) in table" :key="key" >
                                        <td v-for="(item,index) in value" :key="index" style="height:34px;"  v-html="item.toString().replace(/\B(?=(\d{3})+(?!\d))/g, '.')"></td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </vx-card>
        </div>
    </div>
</template>

<script>
import Datepicker from 'vuejs-datepicker'
import vcheckboxpopup from '../components/CheckboxPopup'
import XLSX from 'xlsx'

export default {
    data:function(){
        return{
            uri :'data:application/vnd.ms-excel;base64,',
            template:'<html xmlns:o="urn:schemas-microsoft-com:office:office" xmlns:x="urn:schemas-microsoft-com:office:excel" xmlns="http://www.w3.org/TR/REC-html40"><head><!--[if gte mso 9]><xml><x:ExcelWorkbook><x:ExcelWorksheets><x:ExcelWorksheet><x:Name>{worksheet}</x:Name><x:WorksheetOptions><x:DisplayGridlines/></x:WorksheetOptions></x:ExcelWorksheet></x:ExcelWorksheets></x:ExcelWorkbook></xml><![endif]--></head><body><table>{table}</table></body></html>',
            base64: function(s){ return window.btoa(unescape(encodeURIComponent(s))) },
            format: function(s, c) { return s.replace(/{(\w+)}/g, function(m, p) { return c[p]; }) },
            selectedMonth:new Date(),
            // selectedMonthEnd:new Date(),
            table:[],
            header:null,
            entitas:[],
            selected: [],
            popupActivo:false,
            excell:true,
            search :'',
            export:[],
            option: 'ME'
        }

    },
    
    components:{
        Datepicker,
        vcheckboxpopup
    },

    computed:{
        selectAll: {
            get () {
                return this.selected.length === this.entitas.length
            },

            set (value) {
                this.selected = []

                if (value) {
                this.entitas.forEach((select) => {
                    this.selected.push(select.kode)
                })
                }
            }
        },

        filteredItems() {
            return this.entitas.filter(item => {
                if(item.nama == undefined)
                return false
                return item.nama.toLowerCase().indexOf(this.search.toLowerCase()) > -1
            })
        }
    },

    mounted(){
        this.loadEntitas()
    },

    methods:{
        exportToExcel() {
            var wb = XLSX.utils.book_new() // make Workbook of Excel
            //   XLSX.utils.book_append_sheet(wb, animalWS, 'jan')
            for (var name in this.export) {
                var animalWS = XLSX.utils.json_to_sheet(this.export[name].data)
                XLSX.utils.book_append_sheet(wb, animalWS, this.export[name].bulan) // sheetAName is name of Worksheet 
                // console.log(this.export[name].bulan);
            }

            // export Excel file
            XLSX.writeFile(wb, 'NERACA.xlsx') // name of the file is 'book.xlsx'
            
        },

        loadEntitas() {
            this.entitas = this.$store.state.AppActiveUser.entitas
        },

        tableToExcel(table, name) {
            if (!table.nodeType) table = this.$refs.table
            var ctx = {worksheet: name || 'Worksheet', table: table.innerHTML}
            window.location.href = this.uri + this.base64(this.format(this.template, ctx))
        },

        loadDataServer() {
            if (this.selected.length == 0) {
                this.$swal.fire(
                    'Oopss !',
                    'Anda Belum Memilih Entitas',
                    'error'
                )
            } else {

                let bulanPost = btoa(this.selectedMonth);
                // var entitasId = this.selected.map(x => x.kode);
                let entitasPost = {
                kode_entitas : this.selected,
                }
                
                if(this.option == 'ME') {
                    this.$vs.loading()
                    this.$http.post('neraca/nrckonsolidasi/'+ bulanPost, entitasPost)
                    .then(res => {
                        this.table  = res.data.message
                        // console.log(this.table);
                        this.header = res.data.header
                        this.excell = false
                        //console.log(JSON.stringifythis.header)
                    })
                    .catch(err=>{
                        this.$vs.loading.close()
                        this.$swal.fire(
                            'Gagal !',
                            err.response.data.message,
                            'error'
                        )
                    })
                    .finally(() => {this.$vs.loading.close()})
                } else {
                    // console.log(this.selected)
                    if(this.selected.length > 1) {
                        this.$swal.fire(
                            'Oopss !',
                            'Anda memilih lebih dari 1 Entitas',
                            'error'
                        )
                    } else {
                        this.$vs.loading()
                        this.$http.post('neraca/nrckonsolidasiPerBulan/'+ bulanPost, entitasPost)
                        .then(res => {
                            this.table  = res.data.message
                            // console.log(this.table);
                            this.header = res.data.header
                            this.excell = false
                            //console.log(JSON.stringifythis.header)
                        })
                        .catch(err=>{
                            this.$vs.loading.close()
                            this.$swal.fire(
                                'Gagal !',
                                err.response.data.message,
                                'error'
                            )
                        })
                        .finally(() => {this.$vs.loading.close()})
                    }
                }
            }
        }
    }
}
</script>

<style scoped>
.zui-table {
    border: none;
    border-right: solid 1px #DDEFEF;
    border-collapse: separate;
    border-spacing: 0;
    font: normal 13px Arial, sans-serif;
}

.zui-table thead th {
    border: none;
    padding: 10px;
    text-align: center;
    white-space: nowrap;
}

.zui-table tbody td {
    border-bottom: solid 1px #DDEFEF;
    border-right: solid 1px #DDEFEF;
    color: #333;
    padding: 10px;
    text-shadow: 1px 1px 1px #fff;
    white-space: nowrap;
}

.zui-wrapper {
    position: relative;
}

.zui-scroller {
    margin-left: 0px;
    overflow-x: scroll;
    overflow-y: visible;
    padding-bottom: 5px;
    width: auto;
}

.zui-table .zui-sticky-col {
    border-left: solid 1px #DDEFEF;
    border-right: solid 1px #DDEFEF;
    left: 0;
    position: absolute;
    top: auto;
    width: 280px;
}
</style>